<template>
  <div class="img_spot">
    <!-- ------ -->
    <Header title="" class="app_view_head"></Header>
    <!-- ------ -->
    <div class="app_view_box">
      <div class="query_form">
        <Field label="" placeholder="" v-model="form.invoiceCode"></Field>
        <Field label="" placeholder="" v-model="form.invoiceNumber"></Field>
      </div>
      <ul>
        <li>
          <Button type="primary"></Button>
        </li>
        <li>
          <Button type="default"></Button>
        </li>
        <li>
          <Button @click="JsSdkPZ" type="default"></Button>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
const wx = require("weixin-js-sdk");
import Jssdk from "@/plugins/jssdk";
import axios from "axios";
import { Header, Button, Field } from "mint-ui";
export default {
  name: "ImgSpot",
  components: { Header, Button, Field },
  data() {
    return {
      form: {
        invoiceCode: "",
        invoiceNumber: "",
        date: ""
      }
    };
  },
  methods: {
    /* ------ */
    JsSdkPZ() {
      wx.chooseImage({
        count: 1, // 9
        sizeType: ["original", "compressed"], // 
        sourceType: ["album", "camera"], // 
        success: res => {
          const localIds = res.localIds; // IDlocalIdimgsrc
          this.$router.push({
            name: "indetail",
            query: { src: localIds[0] }
          });
        }
      });
    }
  },
  mounted() {
    /* ------ */
    Jssdk(wx, axios);
  }
};
</script>
<style lang="less">
.img_spot {
  li {
    height: 0.5rem;
    text-align: center;
    button {
      width: 80%;
    }
  }
  /* ------ */
  .query_form {
    margin: 0.3rem 0.4rem 0;
    padding-top: 40px;
  }
}
</style>