import { Indicator } from "mint-ui";
import ModelType from '@/plugins/modelType'

export default (wx, axios) => {
    // 
    Indicator.open({
        text: "...",
        spinnerType: "fading-circle"
    });
    // url
    let url = "";
    ModelType == 0 ? url = location.href : url = location.href.split("#")[0]
    // jssdk
    axios
        .get("/OAutho/JsSdkConfig", {
            params: { page: url }
        })
        .then(res => {
            /* ------ */
            Indicator.close();
            /* ------ */
            wx.config({
                debug: false, // ,apialertpclogpc
                appId: "wx7a6ff0f36feddc5a", // 
                timestamp: res.data.timestamp, // 
                nonceStr: res.data.noncestr, // 
                signature: res.data.signature, // 
                jsApiList: ["chooseImage"] // JS
            });
        }).catch(() => {
            /* ------ */
            Indicator.close();
        })
}